import { BlogBuilder } from "../components/blog/BlogBuilder";
const bloglist = [];

const blog = new BlogBuilder({
  title: "First Post!",
  image: "1",
  description:
    "Just revamped my profile and thought I'd attach a blog along with it.",
})
  .addHeading("A New Beginning")
  .addParagraph(
    "Just revamped my profile and thought I'd attach a blog along with it."
  )
  .addParagraph(
    "I'm going to treat this more like a journal at the moment and record things of interest that I come across every now and then. To the person reading this - I hope I kept my promise and there are other interesting posts here worth reading."
  );

const blog1 = new BlogBuilder({
  title: "The No Code Movement",
  image: "2",
  description:
    "The No Code Movement might be the latest trend, but it's nothing new.",
})
  .addHeading("A Natural Consequence")
  .addParagraph(
    "We've come a long way from the early days of electronics. We went through punch cards, binary, assembly, C, C++, Java, JavaScript, Python and now have new-comers with promises of flexibility in ease of use with increased control in Go and more so, Rust."
  )
  .addParagraph(
    "It's been getting increasingly easier for people to get into coding with the rise of high-level programming languages as well as shared libraries and scaffolding frameworks, which let designers narrow their attention towards their main scope. The No Code movement is a trendy term for a movement that has been in place since the birth of human ingenuity and technology."
  )
  .addHeading("That's Cool. So Can I Leave It All To Tech Eventually?")
  .addParagraph(
    "Not really. Life is obviously a lot easier than what it was ever before, but there's still a lot to be done. This glorious tech is built by our best minds, who are still human and prone to errors and there are a lot more errors to fix and improvements to be made than what all humans can currently handle. What you can do to contribute is share your domain expertise and help in patching bugs in our existing frameworks."
  )
  .addParagraph(
    "We are at a very nascent stage when it comes to AI and have to create plenty more useful datasets for artificial models to consume."
  );

const blog2 = new BlogBuilder({
  title: "Objective Truth",
  image: "3",
  description: "What is truth? An affirmation of our collective perceptions?",
})
  .addQuote(
    "`A proposition is considered to have objective truth when its truth conditions are met without bias caused by a sentient subject. Scientific objectivity refers to the ability to judge without partiality or external influence.`"
  )
  .addParagraph(
    "I like this description of truth because of how it recursively contradicts itself. Continuing in this line of thought, I'll assert that an objective truth is a sum of multifarious subjective ones."
  );

bloglist.push(blog2);
bloglist.push(blog1);
bloglist.push(blog);

export default bloglist;
